* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(#FEFFFF, #A7CECC);
  height: 100vh;
}

#search-box {
  display: block;
  font: inherit;
  font-size: 24px;
  font-weight: 500;
  padding: 10px;
  max-width: 500px;
  margin: 20px auto;
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05), 0 15px 40px rgba(0, 0, 0, 0.1);
}
#search-box:focus {
  outline: none;
}
h2 {
  margin: 0;
}
.country-list {
  padding: 0 30px;
  list-style: none;
  font-size: 20px;
}
.country-list li {
  padding-bottom: 10px;
}
.country-card {
  font-size: 20px;
  padding: 30px;
}
.country-title-box {
  display: flex;
  align-items: baseline;
}
.country-flag {
  margin-right: 5px;
}
p {
  font-weight: 700;
}
span {
  font-weight: 400;
}

.languages-item:not(:last-child):after {
content: ',';
}
.country-title {
  font-weight: 400;
}